"use client";

import BoletinPDF from "./boletin/BoletinPDF";
import { useAuth } from "../../context/authContext";
import styles from "../../styles/admin-dashboard.module.css";
import { useEffect, useState } from "react";
import Link from 'next/link';
import { useRouter } from 'next/navigation';
import Button from '../../components/ui/Button';
import LoadingModal from '../../components/ui/LoadingModal';
import useDelayedOpen from '../../hooks/useDelayedOpen';

export default function EstudianteDashboard() {
  const { user, loading, logout } = useAuth();
  const router = useRouter();
  const [cicloId] = useState(1);
  const [horario, setHorario] = useState<Array<any>>([]);
  const [notas, setNotas] = useState<Array<any>>([]);
  const [asignaturas, setAsignaturas] = useState<Array<string>>([]);
  const [loadingData, setLoadingData] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const delayedOpen = useDelayedOpen(loading);
  const delayedDataOpen = useDelayedOpen(loadingData);
  
  useEffect(() => {
    // Cuando el usuario est√© disponible, traemos horario y notas
    const fetchData = async () => {
      if (!user) return;
      setLoadingData(true);
      setError(null);
      try {
        const token = await user.getIdToken();
        const headers = { Authorization: `Bearer ${token}` };

        // Traer horario
        const hRes = await fetch('/api/estudiante/horario', { headers });
        if (!hRes.ok) throw new Error('Error al obtener horario');
        const hData = await hRes.json() as any[];
        setHorario(Array.isArray(hData) ? hData : []);

        // Traer notas
        const nRes = await fetch('/api/estudiante/notas', { headers });
        if (!nRes.ok) throw new Error('Error al obtener notas');
        const nData = await nRes.json() as any[];
        setNotas(Array.isArray(nData) ? nData : []);

        // Derivar asignaturas: tomar de horario primero, si no, de notas
        const materiasFromHorario = Array.from(new Set((hData || []).map((h: any) => String(h.materia)))) as string[];
        if (materiasFromHorario.length) {
          setAsignaturas(materiasFromHorario as string[]);
        } else {
          const materiasFromNotas = Array.from(new Set((nData || []).map((n: any) => n.materia?.nombre).filter(Boolean)));
          setAsignaturas(materiasFromNotas as string[]);
        }

      } catch (err: any) {
        console.error(err);
        setError(err.message || 'Error cargando datos');
      } finally {
        setLoadingData(false);
      }
    };

    fetchData();
  }, [user]);

  if (loading) return <div className={styles.loadingContainer}><LoadingModal open={delayedOpen} message="Cargando..." /></div>;
  if (!user) return <div className={styles.loadingContainer}><h2>Bienvenido a AulaUnida</h2><p>Por favor inicia sesi√≥n.</p></div>;

  return (
    <div className={styles.dashboardContainer}>
      <aside className={styles.sidebar}>
        <div style={{ display: 'flex', alignItems: 'center', marginBottom: '3rem' }}>
          <img src="/default-avatar.png" alt="avatar" className={styles.avatar} />
          <span className={styles.logo}>AulaUnida</span>
        </div>
        <nav className={styles.menu}>
          <ul style={{ listStyle: 'none', padding: 0, margin: 0 }}>
            <li>
              <Link href="/estudiante" className={`${styles.menuItem} ${styles.menuItemActive}`}>
                <span style={{ marginRight: 16 }}>üè†</span>Inicio
              </Link>
            </li>
            <li>
              <Link href="/estudiante/asignaturas" className={styles.menuItem}>
                <span style={{ marginRight: 16 }}>üìö</span>Asignaturas
              </Link>
            </li>
            <li>
              <Link href="/estudiante/notas" className={styles.menuItem}>
                <span style={{ marginRight: 16 }}>üìä</span>Notas y Asistencia
              </Link>
            </li>
            <li>
              <Link href="/estudiante/boletin" className={styles.menuItem}>
                <span style={{ marginRight: 16 }}>üìã</span>Bolet√≠n
              </Link>
            </li>
          </ul>
        </nav>
        <button onClick={async () => { await logout(); router.push('/login'); }} className={styles.logoutBtn}>
          <span style={{ marginRight: 8 }}>‚éã</span> Cerrar sesi√≥n
        </button>
      </aside>

      <main className={styles.mainContent}>
        <h1 className={styles.title}>Panel de Estudiante</h1>
        <p className={styles.subtitle}>Bienvenida de nuevo, {user.displayName?.split(' ')[0] || 'estudiante'}.</p>

        <div style={{ display: 'grid', gridTemplateColumns: '2fr 1fr', gap: '2rem', marginTop: '2rem' }}>
          {/* Columna izquierda - Tareas y Clases */}
          <div>
            {/* Tareas Pendientes */}
            <div className={styles.activityCard} style={{ marginBottom: '2rem' }}>
              <h2 className={styles.activityTitle}>Tareas Pendientes</h2>
              <div style={{ marginTop: '1.5rem' }}>
                {!loadingData && asignaturas.length === 0 && (
                  <div style={{ color: 'var(--text-muted)', textAlign: 'center', padding: '2rem' }}>
                    No hay tareas pendientes
                  </div>
                )}
                {!loadingData && asignaturas.map((asignatura, i) => (
                  <div key={i} style={{ 
                    display: 'flex', 
                    alignItems: 'center', 
                    padding: '1rem',
                    backgroundColor: 'var(--bg-hover)',
                    borderRadius: '12px',
                    marginBottom: '0.75rem',
                    border: '1px solid var(--bg-hover)'
                  }}>
                    <div style={{
                      width: '40px',
                      height: '40px',
                      backgroundColor: 'var(--green-primary)',
                      borderRadius: '8px',
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      marginRight: '1rem'
                    }}>
                      üìÑ
                    </div>
                    <div style={{ flex: 1 }}>
                      <div style={{ 
                        fontWeight: '600',
                        color: 'var(--text-primary)',
                        marginBottom: '0.25rem'
                      }}>
                        Resolver ejercicios de {asignatura}
                      </div>
                      <div style={{ 
                        color: 'var(--text-secondary)',
                        fontSize: '0.875rem'
                      }}>
                        {asignatura}
                      </div>
                    </div>
                    <div style={{
                      color: '#fbbf24',
                      fontSize: '0.875rem',
                      fontWeight: '500'
                    }}>
                      Vence: {i === 0 ? '2 d√≠as' : '5 d√≠as'}
                    </div>
                  </div>
                ))}
              </div>
            </div>

            {/* Pr√≥ximas Clases */}
            <div className={styles.activityCard}>
              <h2 className={styles.activityTitle}>Pr√≥ximas Clases</h2>
              <div style={{ marginTop: '1.5rem' }}>
                {!loadingData && horario.length === 0 && (
                  <div style={{ color: 'var(--text-muted)', textAlign: 'center', padding: '2rem' }}>
                    No hay clases programadas
                  </div>
                )}
                {!loadingData && horario.map((clase, i) => (
                  <div key={i} style={{ 
                    display: 'flex', 
                    alignItems: 'center', 
                    justifyContent: 'space-between',
                    padding: '1rem',
                    backgroundColor: 'var(--bg-hover)',
                    borderRadius: '12px',
                    marginBottom: '0.75rem',
                    border: '1px solid var(--bg-hover)'
                  }}>
                    <div style={{ display: 'flex', alignItems: 'center' }}>
                      <div style={{
                        width: '40px',
                        height: '40px',
                        backgroundColor: 'var(--green-primary)',
                        borderRadius: '8px',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        marginRight: '1rem'
                      }}>
                        üìÖ
                      </div>
                      <div>
                        <div style={{ 
                          fontWeight: '600',
                          color: 'var(--text-primary)',
                          marginBottom: '0.25rem'
                        }}>
                          {clase.materia}
                        </div>
                        <div style={{ 
                          color: 'var(--text-secondary)',
                          fontSize: '0.875rem'
                        }}>
                          Profesor: {clase.docente || 'Dr. Mart√≠nez'}
                        </div>
                      </div>
                    </div>
                    <div style={{
                      color: 'var(--text-primary)',
                      fontSize: '0.875rem',
                      fontWeight: '500'
                    }}>
                      {clase.hora || '10:00 AM'}
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* Columna derecha - Notificaciones */}
          <div>
            <div className={styles.activityCard}>
              <h2 className={styles.activityTitle}>Notificaciones</h2>
              <div style={{ marginTop: '1.5rem' }}>
                {/* Notificaciones hardcoded para el ejemplo, como en la imagen */}
                <div style={{ 
                  display: 'flex', 
                  alignItems: 'flex-start', 
                  padding: '1rem',
                  marginBottom: '0.75rem'
                }}>
                  <div style={{
                    width: '8px',
                    height: '8px',
                    backgroundColor: '#ef4444',
                    borderRadius: '50%',
                    marginRight: '1rem',
                    marginTop: '0.5rem'
                  }}></div>
                  <div>
                    <div style={{ 
                      fontWeight: '600',
                      color: 'var(--text-primary)',
                      marginBottom: '0.25rem'
                    }}>
                      Examen de Matem√°ticas
                    </div>
                    <div style={{ 
                      color: 'var(--text-secondary)',
                      fontSize: '0.875rem'
                    }}>
                      Fecha: 15 de mayo
                    </div>
                  </div>
                </div>

                <div style={{ 
                  display: 'flex', 
                  alignItems: 'flex-start', 
                  padding: '1rem',
                  marginBottom: '0.75rem'
                }}>
                  <div style={{
                    width: '8px',
                    height: '8px',
                    backgroundColor: '#3b82f6',
                    borderRadius: '50%',
                    marginRight: '1rem',
                    marginTop: '0.5rem'
                  }}></div>
                  <div>
                    <div style={{ 
                      fontWeight: '600',
                      color: 'var(--text-primary)',
                      marginBottom: '0.25rem'
                    }}>
                      Entrega de proyecto de Ciencias
                    </div>
                    <div style={{ 
                      color: 'var(--text-secondary)',
                      fontSize: '0.875rem'
                    }}>
                      Fecha: 20 de mayo
                    </div>
                  </div>
                </div>

                <div style={{ 
                  display: 'flex', 
                  alignItems: 'flex-start', 
                  padding: '1rem',
                  marginBottom: '0.75rem'
                }}>
                  <div style={{
                    width: '8px',
                    height: '8px',
                    backgroundColor: 'var(--green-primary)',
                    borderRadius: '50%',
                    marginRight: '1rem',
                    marginTop: '0.5rem'
                  }}></div>
                  <div>
                    <div style={{ 
                      fontWeight: '600',
                      color: 'var(--text-primary)',
                      marginBottom: '0.25rem'
                    }}>
                      Nuevas calificaciones disponibles
                    </div>
                    <div style={{ 
                      color: 'var(--text-secondary)',
                      fontSize: '0.875rem'
                    }}>
                      Asignatura: Historia
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* centralized loading modal for data fetches */}
        <LoadingModal open={delayedDataOpen} message="Cargando datos..." />
      </main>
    </div>
  );
}